import React, { useEffect, useState, useRef } from "react";
import "./App.scss";
import { Route, withRouter, useHistory } from "react-router-dom";
import firebase from "./firebase";
import { DocumentData } from "firebase/firestore";

import SignUp from "./components/signup";
import Login from "./components/login";
import Main from "./components/main";
import Setting from "./components/setting";
import Notice from "./components/notice";
import Version from "./components/version";
import PlayList from "./components/playlist";
import AlbumEdit from "./components/albumEdit";
import MusicList from "./components/musiclist";
import AddMusic from "./components/addMusic";

//uesState type
type Account = {
  email: string;
  password: string;
};
type PlayListType = {
  id: number;
  title: string;
  info: string;
  playList: {
    title: string;
    singer: string;
  }[];
  active: boolean;
}[];
type Music = {
  title: string;
  singer: string;
  url: string;
}[];

// Ìï®Ïàò type
type OnChange = (e: React.ChangeEvent<HTMLInputElement>) => void;
type CreateUser = (email: string, password: string) => void;
type LoginType = (email: string, password: string) => void;
type LogOutType = () => void;
type OnModal = (id: number) => void;
type UpLoading = () => void;
type AlbumRemove = (id: number) => void;

// export signup.tsx
export interface SignUpIprops {
  createUser: CreateUser;
  account: Account;
  onChange: OnChange;
}
//export login.tsx
export interface LoginIprops {
  login: LoginType;
  account: Account;
  onChange: OnChange;
}
//export playlist.tsx
export interface PlayListIprops {
  album: PlayListType;
  onModal: OnModal;
  albumRemove: AlbumRemove;
}
// export main.tsx
export interface MainIprops {
  album: PlayListType;
  num: number;
  nextNum: number;
  music: Music;
}
// action album up, down
export interface ActionIprops {
  changeAlbum: {
    nextAlbum: () => void;
    beforeAlbum: () => void;
  };
  changeMusic: {
    nextMusic: () => void;
    beforeMusic: () => void;
  };
}
// albumEdit.tsx
export interface AlbumEditIprops {
  album: PlayListType;
}

// musiclist.tsx
export interface MusicListIprops {
  onChangeMusic: OnChange;
  upLoadMusic: UpLoading;
  on: boolean;
  music: Music;
}

// setting.tsx
export interface SettingIprops {
  logout: LogOutType;
}
// addmusic.tsx
export interface AddMusicIprops {
  music: Music;
}

function App() {
  const history = useHistory();
  const [account, setAccount] = useState<Account>({ email: "", password: "" });
  const { email, password } = account;

  // input.valueÎ•º account stateÏóê Ï†ÄÏû•.
  const onChange: SignUpIprops["onChange"] = function (e) {
    setAccount({ ...account, [e.target.name]: e.target.value });
    // console.log(account);
  };
  // Í≥ÑÏ†ïÎßåÎì§Í∏∞ function
  const createUser: SignUpIprops["createUser"] = async function () {
    await firebase
      .auth()
      .createUserWithEmailAndPassword(email, password)
      .then(() => {
        console.log("Í∞ÄÏûÖ ÏÑ±Í≥µ!");
        setAccount({ email: "", password: "" });
        history.replace("/");
      })
      .catch(() => {
        console.log("Í∞ÄÏûÖ Ïã§Ìå®!");
      });
  };

  // Î°úÍ∑∏Ïù∏ Í∏∞Îä•
  const login: LoginIprops["login"] = function (email, password) {
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(() => {
        console.log("Î°úÍ∑∏Ïù∏ÏÑ±Í≥µ!");
        setAccount({ email: "", password: "" });
        history.push("/main");
      })
      .catch(() => {
        console.log("Îã§Ïãú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî..");
      });
  };

  //Î°úÍ∑∏ÏïÑÏõÉ Ìï®Ïàò.
  const logOut: SettingIprops["logout"] = function () {
    firebase
      .auth()
      .signOut()
      .then(() => {
        console.log("Î°úÍ∑∏ÏïÑÏõÉ ÌïòÏÖ®ÏäµÎãàÎã§.");
        history.replace("/");
      });
  };

  // ÏùåÏïÖ Î≥¥Í¥ÄÏÜå.
  const [music, setMusic] = useState<Music>([
    {
      title: "",
      singer: "",
      url: "",
    },
  ]);
  // firestoreÏóêÏÑú ÏùåÏïÖ Í∞ÄÏ†∏Ïò§Í∏∞
  useEffect(() => {
    var arr: { title: string; singer: string; url: string }[] = [];
    firebase
      .firestore()
      .collection("playList")
      .get()
      .then((snapshot) => {
        snapshot.forEach((doc: DocumentData) => {
          arr.push(doc.data());
        });
      });
    setMusic(arr);
  }, []);
  console.log(music);
  // const [{ title, singer, url }] = music;
  // Main Action Î≤ÑÌäº Ï°∞Ï†à state.
  let [num, setNum] = useState<MainIprops["num"]>(0);
  let [nextNum, setNextNum] = useState<MainIprops["nextNum"]>(0);

  // ÎÇòÏùò Ïï®Î≤î state.
  const [album, setAlbum] = useState<PlayListType>([
    {
      id: 0,
      title: "favorite",
      info: "glen check, ÌïúÏöîÌïú, 10cm..",
      playList: [
        {
          title: "Ive got this feeling",
          singer: "Glen Check",
        },
        {
          title: "ÎûåÎ≥¥Î•¥Í∏∞Îãà",
          singer: "Han Yo Han",
        },
        {
          title: "paint it gold",
          singer: "Glen Check",
        },
      ],
      active: false,
    },
    {
      id: 1,
      title: "rock balad",
      info: "buzz, ÍπÄÏÉÅÎØº..",
      playList: [
        {
          title: "ÎÇ®ÏûêÎ•º Î™∞Îùº",
          singer: "Î≤ÑÏ¶à",
        },
        {
          title: "YOU",
          singer: "ÍπÄÏÉÅÎØº",
        },
        {
          title: "Í∞ÄÏãú",
          singer: "buzz",
        },
      ],
      active: false,
    },
  ]);
  // Ïï®Î≤îÏÑ§Ï†ï Ï∂îÍ∞Ä, ÏÇ≠Ï†ú Î™®Îã¨.
  const onModal: OnModal = function (id) {
    setAlbum(
      album.map((list) => {
        return list.id === id ? { ...list, active: !list.active } : list;
      })
    );
  };
  // Ïï®Î≤î Ï†úÍ±∞ Ìï®Ïàò.
  const albumRemove: PlayListIprops["albumRemove"] = function (id) {
    setAlbum(
      album.filter((album) => {
        return album.id !== id;
      })
    );
  };

  // action playlist up, down Î≤ÑÌäºÍ∏∞Îä•.
  const changeAlbum: ActionIprops["changeAlbum"] = {
    nextAlbum: function () {
      if (nextNum < album.length - 1) {
        setNum(num + 1);
      } else {
        return num;
      }
    },
    beforeAlbum: function () {
      if (num > 0) {
        setNum(num - 1);
      } else {
        return num;
      }
    },
  };
  // action playlist next, before Music Î≤ÑÌäºÍ∏∞Îä•.
  const changeMusic: ActionIprops["changeMusic"] = {
    nextMusic: function () {
      if (nextNum < album[num].playList.length - 1) {
        setNextNum(nextNum + 1);
      } else {
        return nextNum;
      }
    },
    beforeMusic: function () {
      if (nextNum > 0) {
        setNextNum(nextNum - 1);
      } else {
        return nextNum;
      }
    },
  };

  // ÏùåÏïÖ Ï†ÄÏû•ÌïòÍ≥† Ï≤¥ÌÅ¨ÌïòÎäî Î≤ÑÌäºstate.
  const [on, setOn] = useState<MusicListIprops["on"]>(false);
  // ÎÖ∏Îûò Ï†ÄÏû•ÌïòÍ∏∞.
  const [musicFile, setFiles] = useState(null);
  // firebase storage.
  const storage = firebase.storage();
  // ÏùåÏïÖ ÌååÏùº Î∞õÏïÑÏò§Îäî Ìï®Ïàò.
  const onChangeMusic: OnChange = (e) => {
    setFiles(e.target.files[0]);
    setOn(!on);
  };

  // üéµÎÖ∏Îûò ÏóÖÎ°úÎìú Í∏∞Îä•üéµ.
  const nextId = useRef(-1);
  const upLoadMusic: UpLoading = function () {
    const storageRef = storage.ref();
    const downLoadPath = storageRef.child("music/" + musicFile.name);
    const upLoading = downLoadPath.put(musicFile);
    upLoading.on(
      "state_changed",
      // Î≥ÄÌôîÌï† Îïå, ÎèôÏûëÌïòÎäî Ìï®Ïàò.
      (loading) => {
        console.log("Î°úÎî©Ï§ë.." + loading);
      },
      //ÏóêÎü¨Ïãú ÎèôÏûëÌïòÎäî Ìï®Ïàò.
      (error) => {
        console.log("Ïã§Ìå®ÏÇ¨Ïú†: ", error);
      },
      // ÏÑ±Í≥µÏãú ÎèôÏûëÌïòÎäî Ìï®Ïàò.
      () => {
        upLoading.snapshot.ref.getDownloadURL().then((url) => {
          console.log("ÏóÖÎ°úÎìú ÏÑ±Í≥µ!");
          // const item = {
          //   title: musicFile.name.split("-")[1],
          //   singer: musicFile.name.split("-")[0],
          //   url: url,
          // };
          // // firestoreÏóê title,singer,url Î≥¥ÎÇ¥ÎäîÍ±∞ Ï∂îÍ∞ÄÌïòÍ∏∞.
          // setMusic([...music, item]);
          setOn(!on);
          // setFiles(null);
          // console.log("ÏóÖÎ°úÎìúÎêú Í≤ΩÎ°úÎäî", url);

          // firestoreÏóê textÎ°ú Ï†ÄÏû•.
          const db = firebase.firestore();
          db.collection("playList")
            .doc(musicFile.name)
            .set({
              title: musicFile.name.split("-")[1],
              singer: musicFile.name.split("-")[0],
              url: url,
            });
          // Ïûò Ï†ÄÏû• ÎêòÏóàÎäîÏßÄ Ï∂úÎ†•.
          // db.collection("playList")
          //   .get()
          //   .then((result) => {
          //     result.forEach((doc) => {
          //       console.log(doc.data());
          //     });
          //   });
        });
      }
    );
  };

  return (
    <div className="App">
      <Nav album={album} num={num} nextNum={nextNum} music={music} />
      <Route exact path="/">
        <Login login={login} account={account} onChange={onChange} />
      </Route>
      <Route path="/signup">
        <SignUp createUser={createUser} account={account} onChange={onChange} />
      </Route>
      <Route path="/main">
        <Main album={album} num={num} nextNum={nextNum} music={music} />
        <Actions changeAlbum={changeAlbum} changeMusic={changeMusic} />
      </Route>

      <Route path="/setting">
        <Setting logout={logOut} />
      </Route>

      {/* setting */}
      <Route path="/notice">
        <Notice />
      </Route>
      <Route path="/version">
        <Version />
      </Route>
      <Route exact path="/playlist">
        <PlayList album={album} onModal={onModal} albumRemove={albumRemove} />
      </Route>
      <Route path="/playlist/:id">
        <AlbumEdit album={album} />
      </Route>
      <Route path="/addmusic/:id">
        <AddMusic music={music} />
      </Route>
      <Route path="/musiclist">
        <MusicList
          onChangeMusic={onChangeMusic}
          upLoadMusic={upLoadMusic}
          on={on}
          music={music}
        />
      </Route>
    </div>
  );
}

const Nav: React.FC<MainIprops> = function (props): JSX.Element {
  const history = useHistory();
  const [navList, setNavList] = useState([
    {
      id: 0,
      title: "",
      site: "/main",
    },
    {
      id: 1,
      title: "ÏÑ§Ï†ï",
      site: "/setting",
    },
    {
      id: 2,
      title: "Í≥µÏßÄÏÇ¨Ìï≠",
      site: "/notice",
    },
    {
      id: 3,
      title: "Î≤ÑÏ†ÑÏ†ïÎ≥¥",
      site: "/version",
    },
    {
      id: 4,
      title: "Ïï®Î≤î",
      site: "/playlist",
    },
    {
      id: 5,
      title: "Ïï®Î≤îÌé∏Ïßë",
      site: "/playlist/:id",
    },
    {
      id: 6,
      title: "Í≥° Î¶¨Ïä§Ìä∏",
      site: "/musiclist",
    },
    {
      id: 7,
      title: "Ïï®Î≤îÎÖ∏ÎûòÏ∂îÍ∞Ä",
      site: "/addmusic/:id",
    },
  ]);
  return (
    <div id="nav">
      {navList.map((navList) => {
        return (
          <Route exact path={navList.site}>
            <i
              onClick={() => {
                history.goBack();
              }}
              className="fas fa-chevron-left"
            ></i>
            <span>
              {navList.site === "/main"
                ? (navList.title = props.album[props.num].title)
                : navList.title}
            </span>
            {navList.site === "/playlist/:id" ? (
              <p className="edits">ÏôÑÎ£å</p>
            ) : (
              <i
                onClick={() => {
                  history.push("/setting");
                }}
                className="fas fa-cog edits"
              ></i>
            )}
          </Route>
        );
      })}
    </div>
  );
};

const Actions: React.FC<ActionIprops> = function (props): JSX.Element {
  const [play, setPlay] = useState(false);
  return (
    <div id="actions">
      <section className="up-btn">
        <i
          onClick={() => {
            props.changeAlbum.beforeAlbum();
          }}
          className="fas fa-chevron-up"
        ></i>
      </section>
      <section className="middle-btn">
        <i
          onClick={() => {
            props.changeMusic.beforeMusic();
          }}
          className="fas fa-chevron-left"
        ></i>
        {play ? (
          <i
            onClick={() => {
              setPlay(!play);
            }}
            className="fas fa-pause play-btn"
          ></i>
        ) : (
          <i
            onClick={() => {
              setPlay(!play);
            }}
            className="fas fa-play play-btn"
          ></i>
        )}

        <i
          onClick={() => {
            props.changeMusic.nextMusic();
          }}
          className="fas fa-chevron-right"
        ></i>
      </section>
      <section className="bottom-btn">
        <i
          onClick={() => {
            props.changeAlbum.nextAlbum();
          }}
          className="fas fa-chevron-down"
        ></i>
      </section>
    </div>
  );
};

export default withRouter(App);
